body {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    font-family: Arial, sans-serif;
  }
  
  .app {
    text-align: center;
  }
  
  h1 {
    margin-bottom: 1.5rem;
    font-size: 2rem;
  }
  
  form {
    margin-bottom: 1.5rem;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  input[type='text'] {
    padding: 0.5rem;
    font-size: 1.2rem;
  }
  
  button {
    padding: 0.5rem 1rem;
    font-size: 1.2rem;
    background-color: #4caf50;
    color: white;
    border: none;
    cursor: pointer;
  }
  
  .weather-card {
    background-color: #f5f5f5;
    border-radius: 5px;
    padding: 1.5rem;
    max-width: 400px;
    margin: 0 auto;
    position: relative;
  }
  
  .weather-info {
    margin-top: 1.5rem;
  }
  
  .temperature,
  .condition {
    margin-bottom: 0.5rem;
    font-size: 1.2rem;
  }
  
  .error {
    color: red;
    font-size: 1.2rem;
  }
  .weather-icon {
    position: absolute;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);
    width: 100px;
    height: 100px;
    animation-duration: 3s;
    animation-iteration-count: infinite;
  }
  
  .sunny {
    animation-name: spin;
  }
  
  .cloudy {
    animation-name: float;
  }
  
  .raining {
    animation-name: fall;
  }
  
  .snowy {
    animation-name: snow;
  }
  
  @keyframes spin {
    0% {
      transform: translateX(-50%) rotate(0deg);
    }
    100% {
      transform: translateX(-50%) rotate(360deg);
    }
  }
  
  @keyframes float {
    0% {
      transform: translateX(-50%) translateY(0);
    }
    50% {
      transform: translateX(-50%) translateY(-5px);
    }
    100% {
      transform: translateX(-50%) translateY(0);
    }
  }
  
  @keyframes fall {
    0% {
      transform: translateX(-50%) translateY(-200px);
    }
    100% {
      transform: translateX(-50%) translateY(0);
    }
  }
  
  @keyframes snow {
    0% {
      transform: translateX(-50%) translateY(-200px);
    }
    100% {
      transform: translateX(-50%) translateY(0);
    }
  }